// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'
import UiLoader from '@components/ui-loader'
// ** Table Columns

// ** Store & Actions
import { GetUserTransaction } from '../../../../../store/actions'
import { useSelector, useDispatch } from 'react-redux'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import BreadCrumbs from '@components/breadcrumbs'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Input, Label, Row, Col, CardHeader, CardTitle } from 'reactstrap'
import { useParams } from 'react-router-dom'

const DataTableTransaction = () => {

  // ** Store Vars

  const dispatch = useDispatch()
  const store = useSelector(state => state.users)
  const params = useParams()
  
  // ** States

  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [searchValue, setSearchValue] = useState('')
  const [filteredData, setFilteredData] = useState([])
  
  useEffect(() => {
    dispatch(GetUserTransaction(params.id, currentPage))
  }, [currentPage])
  // ** Get data on mount

  // ** Function to handle filter
  const handleFilter = e => {
    const value = e.target.value
    let updatedData = []
    setSearchValue(value)
    if (value.length) {
      updatedData = store.transactions.data.filter(item => {
        console.log(item)
        const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric',  hour: 'numeric', minute: 'numeric', second: 'numeric'}) => {
          if (!value) return value 
          return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))         
        }
        const date = formatDate(item.transaction_time)
        const startsWith =
          item.amount?.toString().toLowerCase().startsWith(value.toLowerCase()) ||
          item.balance?.toString().toLowerCase().startsWith(value.toLowerCase()) ||
          item.debit_credit?.toLowerCase().startsWith(value.toLowerCase()) ||
          date?.toString().toLowerCase().startsWith(value.toLowerCase()) ||
          item.transaction_type?.toLowerCase().startsWith(value.toLowerCase()) ||
          item.txn_id.toLowerCase()?.startsWith(value.toLowerCase())

        const includes =
          item.amount?.toString().toLowerCase().includes(value.toLowerCase()) ||
          item.balance?.toString().toLowerCase().includes(value.toLowerCase()) ||
          item.debit_credit?.toLowerCase().includes(value.toLowerCase()) ||
          date?.toString().toLowerCase().includes(value.toLowerCase()) ||
          item.transaction_type?.toLowerCase().includes(value.toLowerCase()) ||
          item.txn_id?.toLowerCase().includes(value.toLowerCase())

        if (startsWith) {
          return startsWith
        } else if (!startsWith && includes) {
          return includes
        } else return null
      })
      setFilteredData(updatedData)
    }
  }

  // ** Function to handle Pagination and get data

  const handlePagination = page => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function to handle per page

  const handlePerPage = e => {
    setRowsPerPage(parseInt(e.target.value))
  }

  // ** Custom Pagination
  const CustomPagination = () => {

     const count = store.transactions.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }
console.log(store.transactions.data)
  // ** Table data to render
  const dataToRender = () => {
    if (store.transactions.data?.length > 0) {
      return searchValue.length ? filteredData : store.transactions.data?.slice(0, rowsPerPage)
    } else {
      return []
    }
  }

  if (store.loadAllUsers) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }
  console.log(store)

  const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric',  hour: 'numeric', minute: 'numeric', second: 'numeric'}) => {
    if (!value) return value 
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
   
  }

  const columns = [
    {
      name: 'Sno',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) => `#${store.transactions.from + pos}`
    }, 
    {
      name: 'Date',
      minWidth: '100px',
      selector: 'transaction_time',
      sortable: true,
      cell: (row) => formatDate(row?.transaction_time)
    },
    {
      name: 'Tax Id',
      minWidth: '100px',
      selector: 'txn_id',
      sortable: true,
      cell: (row) => row?.txn_id
    },
    {
      name: 'Method',
      minWidth: '100px',
      selector: 'debit_credit',
      sortable: true,
      cell: (row) => row?.debit_credit
    },
    {
      name: 'Type',
      minWidth: '100px',
      selector: 'transaction_type',
      sortable: true,
      cell: (row) => row?.transaction_type
    },
    {
      name: 'Amount',
      minWidth: '100px',
      selector: 'amount',
      sortable: true,
      cell: (row) => row?.amount
    },
    {
      name: 'Balance',
      minWidth: '100px',
      selector: 'balance',
      sortable: true,
      cell: (row) => row?.balance
    }
  ]

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Manage Users' breadCrumbParent='users'  breadCrumbActive='transactions' />
      <Card>
        <CardHeader className='border-bottom'>
          <CardTitle tag='h4'>Transactions</CardTitle>
        </CardHeader>
        <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={rowsPerPage}
                onChange={e => handlePerPage(e)}
              >
                <option value={10}>10</option>
                <option value={50}>50</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>entries</Label>
            </div>
          </Col>
          <Col className='d-flex align-items-center justify-content-sm-end mt-sm-0 mt-1' sm='6'>
            <Label className='mr-1' for='search-input'>
              Search
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
            />
          </Col>
        </Row>
        <DataTable
          noHeader
          pagination
          paginationServer
          className='react-dataTable'
          columns={columns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={dataToRender()}
        />
      </Card>
    </Fragment>
  )
}

export default memo(DataTableTransaction)
